var handleRenderMap = (function () {
    var map, regionStyle, markerStyle;
    return function () {
        if (!map) {
            map = $('#jvectorMap').vectorMap({
                map: 'world_mill',
                normalizeFunction: 'polynomial',
                hoverOpacity: 0.5,
                hoverColor: true,
                zoomOnScroll: true,
                backgroundColor: 'transparent',
                markers: userLocations,
                series: {
                    regions: [{
                        values: countryData,
                        scale: [app.color.gray200, app.color.theme],
                        normalizeFunction: 'polynomial'
                    }],
                    markers: [{
                        attribute: 'fill',
                        scale: [app.color.gray200, app.color.theme],
                        normalizeFunction: 'polynomial',
                        values: [1, 5, 10, 20, 30, 40, 50],
                        legend: {
                            vertical: true,
                            title: 'USER COUNT',
                            labelRender: function (v) {
                                return v;
                            }
                        }
                    }]
                },
                regionStyle: regionStyle,
                markerStyle: markerStyle,
                focusOn: {
                    x: 0.5,
                    y: 0.5,
                    scale: 1
                },
                onRegionTipShow: function (e, el, code) {
                    var userCount = countryData[code] || 0;
                    el.html(el.html() + ' (' + userCount + ' users)');
                }
            }).vectorMap('get', 'mapObject');

            regionStyle = {
                initial: {
                    fill: app.color.white,
                    "fill-opacity": 0.35,
                    stroke: 'none',
                    "stroke-width": 0.4,
                    "stroke-opacity": 1
                },
                hover: {
                    "fill-opacity": 0.5
                }
            };

            markerStyle = {
                initial: {
                    fill: app.color.theme,
                    stroke: 'none',
                    "stroke-width": 1.5,
                }
            };
        }
    };
})();

$(document).ready(function () {
    handleRenderMap();
    document.addEventListener('theme-reload', function () {
        $('#jvectorMap').empty();
        handleRenderMap();
    });
});